export const SORT_TYPE = {
  asc: 0,
  desc: 1
}

export default {
  /**
   * @description 
   * @param {Array} 
   * @returns {Number} 
  */
  getAverage(arr) {
    const temp = [...arr];
    if (temp.length > 2) {
      temp.sort((a, b) => a - b);
      temp.shift();
      temp.pop();
    }
    return temp.length ? temp.reduce((sum, cur) => sum + cur) / temp.length : 0;
  },
  /**
   * @description n
   * @param {Array} 
   * @returns n
  */
  getAverageArr(arr) {
    const temp = [];
    const result = [];
    arr.forEach(item => {
      temp.push(item);
      result.push(this.getAverage(temp));
    });
    return result;
  },
  /**
   * @description 
   * 
   * @param {arr: Array, key: String, type: SORT_TYPE} 
   * arr
   * [
   *   {
   *     name: '',
   *     birthday: '1991-09-17'
   *   },
   *   {
   *     name: '',
   *     birthday: '1998-04-16'
   *   }
   * ]
   * key'birthday'
   * typeSORT_TYPE.desc
   * @returns 
  */
  sortByDate(arr, key, type = SORT_TYPE.asc) {
    arr.sort((a, b) => {
      if (type === SORT_TYPE.desc) {
        return new Date(b[key]) - new Date(a[key]);
      }
      return new Date(a[key]) - new Date(b[key]);
    });
    return arr;
  },
  /**
   * @description 
   * 
   * @param {arr: Array, key: String, type: SORT_TYPE} 
   * arr
   * [
   *   {
   *     name: '',
   *     birthday: '1991-09-17'
   *   },
   *   {
   *     name: '',
   *     birthday: '1998-04-16'
   *   }
   * ]
   * key'name'
   * typeSORT_TYPE.desc
   * @returns 
  */
  sortByZH(arr, key, type = SORT_TYPE.asc) {
    const local = 'zh';
    arr.sort((a, b) => {
      if (type === SORT_TYPE.desc) {
        return b[key].localeCompare(a[key], local);
      }
      return a[key].localeCompare(b[key], local);
    });
    return arr;
  },
  /**
   * @description 
   * 
   * @param {arr: Array, key: String, type: SORT_TYPE} 
   * arr
   * [
   *   {
   *     name: '',
   *     birthday: '1991-09-17',
   *     age: 30
   *   },
   *   {
   *     name: '',
   *     birthday: '1998-04-16',
   *     age: 23
   *   }
   * ]
   * key'age'
   * typeSORT_TYPE.desc
   * @returns 
  */
  sortByNum(arr, key, type = SORT_TYPE.asc) {
    arr.sort((a, b) => {
      if (type === SORT_TYPE.desc) {
        return b[key] - a[key];
      }
      return a[key] - b[key];
    });
    return arr;
  }
}